body {
  background-image: linear-gradient(
    to right,
    rgb(48, 0, 102),
    rgb(98, 18, 122)
  );
  color: rgb(255, 255, 255);
}

.fullPhonebook {
  display: flex;
  margin-top: 50px;
}

.deleteButton {
  border: none;
  display: inline-block;
  text-decoration: none;
  text-align: center;
  cursor: pointer;
  color: rgba(255, 0, 0, 0.63);
  background-color: rgb(0, 0, 0);
  margin-left: 20px;
  transition: 0.3s;
  border-radius: 30%;
}

.deleteButton:hover {
  color: white;
  background-color: rgb(255, 0, 0);
}

.addForm {
  display: block;
  padding: 20px;
  width: 300px;
  text-align: center;
}

.addButton {
  border: none;
  display: inline-block;
  text-decoration: none;
  text-align: center;
  cursor: pointer;
  color: rgb(0, 0, 0);
  background-color: rgb(255, 255, 255);
  margin-left: 20px;
  transition: 0.3s;
  border-radius: 10%;
  margin-left: 100px;
  margin-top: 10px;
}
.addButton:hover {
  background-color: green;
  color: white;
}
.phonebook {
  float: right;
  flex: 50%;
}
.phonebookHeader {
  display: block;
  text-align: center;
}

.phonebookList {
  width: 100%;
  display: flex;
  justify-content: center;
}
.number {
  margin-left: 10px;
}
